/* eslint-disable */
import React, { useState, useEffect } from 'react'
import style from './SettingUserForgetPage.module.css'
import axios from 'axios'
import eyeIconOpen from '../../../../assets/static/otherIcon/.svg'
import eyeIconClose from '../../../../assets/static/otherIcon/.svg'
export default function SettingUserForgetPage() {
    const [,setLocalStorageData] = useState({});
    const [inputData, setinputData] = useState({
        user: '',
        email: '',
        code: '',
        password: '',
        password2: ''
    })
    const [isShowCode, setiSHowCode] = useState(false)
    const [showpassword, setshowpassword] = useState(false)
    const [showpassword2, setshowpassword2] = useState(false)

    useEffect(() => {
        if (localStorage.getItem('loginData')) {
            setLocalStorageData(JSON.parse(localStorage.getItem('loginData')))
        }
    }, [])
    const emailHandle = (e) => {
        setinputData({ ...inputData, email: e.target.value })
    }
    const userNameHandle = (e) => {
        setinputData({ ...inputData, user: e.target.value })
    }
    const codeHandle = (e) => {
        setinputData({ ...inputData, code: e.target.value })
    }
    const passwordHandle = (e) => {
        setinputData({ ...inputData, password: e.target.value })
    }
    const password2Handle = (e) => {
        setinputData({ ...inputData, password2: e.target.value })
    }
    const sendCodeHandle = () => {
        const emailPattern = /^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[a-zA-Z]{2,}$/;
        if (inputData.user === "" && emailPattern.test(inputData.email)) {
            alert("")
        } else {
            axios({
                url: 'http://127.0.0.1:4000/forgetpasswordverificationcode',
                method: 'post',
                data: {
                    data: {
                        username: inputData.user,
                        email: inputData.email,
                    }
                }
            }).then(res => {
                if (res.status === 201) {
                    alert(',5')
                    setiSHowCode(!isShowCode)
                }
            }).catch(() => {
                alert('')
            })
        }
    }
    const submitHandler = () => {
        const password = inputData.password === inputData.password2
        if (password) {
            const emailPattern = /^[a-zA-Z0-9]+@[a-zA-Z0-9]+\.[a-zA-Z]{2,}$/;
            if (inputData.user&&emailPattern.test(inputData.email)&&inputData.code&&inputData.password&&inputData.password2) {
                axios({
                    url: 'http://127.0.0.1:4000/forgetpassword',
                    method: 'post',
                    data: {
                        data: {
                            username: inputData.user,
                            email: inputData.email,
                            password: inputData.password,
                            code: inputData.code
                        }
                    }
                }).then(res => {
                    console.log(res.data)
                }).catch(() => {
                    alert('')
                })
            }else{
                alert('')
            }
        } else {
            alert('')
        }
    }
    return (
        <div className={style.SettingUserForgetPage}>
            <form className={style.SettingUserForgetPageForm} onSubmit={(e) => { e.preventDefault() }}>
                <input type='text' placeholder='' className={style.SettingUserForgetPageName} value={inputData.user} onChange={userNameHandle}></input>
                <input type='email' placeholder='' value={inputData.email} onChange={emailHandle}></input>
                {isShowCode ? <input type='code' placeholder='' value={inputData.code} onChange={codeHandle}></input> : null}
                <input type={showpassword ? 'text' : 'password'} placeholder='' value={inputData.password} onChange={passwordHandle}></input>
                {showpassword ?
                    <img className={style.EyeOutlined} onClick={() => setshowpassword(!showpassword)} src={eyeIconOpen} alt="" />
                    :
                    <img className={style.EyeInvisibleOutlined} onClick={() => setshowpassword(!showpassword)} src={eyeIconClose} alt="" />
                }
                <input type={showpassword2 ? 'text' : 'password'} placeholder='' value={inputData.password2} onChange={password2Handle}></input>
                {showpassword2 ?
                    <img className={style.EyeOutlined2} onClick={() => setshowpassword2(!showpassword2)} src={eyeIconOpen} alt="" />
                    :
                    <img className={style.EyeInvisibleOutlined2} onClick={() => setshowpassword2(!showpassword2)} src={eyeIconClose} alt="" />
                }
                <input type='submit' className={style.SettingUserForgetPageFormSendCode} value='' onClick={sendCodeHandle}></input>
                <input type='submit' className={style.SettingUserForgetPageFormSubmitBotton} onClick={submitHandler} ></input>
            </form>
        </div>
    )
}
