
import { auth } from '../config';
//
export const postUpLoad = (id, data) => {
    return auth({
        url: `upload/${id}`,
        data,
        headers: {
            'Content-Type': 'multipart/form-data',
        }
    })
}
//
export const postAddPost = (data) => {
    return auth({
        url: `addpost`,
        data,
    })
}