import React from 'react'
import style from './SettingUser.module.css'
import { useNavigate } from "react-router-dom";
export default function SettingUser() {
    const navigate = useNavigate();
    return (
        <div className={style.SettingUser}>
            <div></div>
            <div onClick={()=>navigate('/setting/account',{replace: true})}></div>
            <div onClick={()=>navigate('/setting/forget',{replace: true})}></div>
            <div onClick={()=>navigate('/setting/deactivate',{replace: true})}></div>
        </div>

    )
}
