import React, { useEffect, useRef, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import PropTypes from 'prop-types';
import { postPostLike, deletePost, postPostfavorite } from '../../../services/post/post'
import useLocalStorage from '../../../hooks/useLocaStorage';
import DeleteBox from '../../../components/DeleteBox';
import likeIcon from '../../../assets/static/postIcon/.svg'
import commentIcon from '../../../assets/static/postIcon/.svg'
import startIcon from '../../../assets/static/postIcon/.svg'
import shareIcon from '../../../assets/static/postIcon/.svg'
import likeIPichcon from '../../../assets/static/postIconPitchUp/.svg'
import commentPichIcon from '../../../assets/static/postIconPitchUp/.svg'
import startPichIcon from '../../../assets/static/postIconPitchUp/.svg'
import sharePichIcon from '../../../assets/static/postIconPitchUp/.svg'
/* props $ */
export default function Post(props) {
    /*                     */
    const navigate = useNavigate();
    const [localStorageData] = useLocalStorage();
    const postDeleteBox = useRef();
    const [targetID, setTargetID] = useState('');
    const [mouseOver, setMouseOver] = useState({
        share: false,
        comments: false,
        star: false,
        like: false,
    });
    useEffect(() => {
        const handleClick = (e) => {
            if (!(e.target === postDeleteBox.current)) {
                setTargetID('');
            }
        }
        window.addEventListener('click', handleClick)
        return () => {
            window.removeEventListener('click', handleClick);
        }
    }, [localStorage])
    // 
    const likehandle = (event) => {
        event.stopPropagation()
        postPostLike(props.id, {
            data: {
                userId: localStorageData.userid
            }
        }).then((response) => {
            console.log(response.data)
            if (response.status === 201) {
                props.reload.setLoad(!props.reload.reload)
            } else if (response.status === 200) {
                alert(response.data.message)
            }
        })
            .catch((error) => {
                console.log(error)
            })
    }
    // 
    const favoritehandler = (e) => {
        e.stopPropagation()
        postPostfavorite(props.id, {
            data: {
                userId: localStorageData.userid
            }
        }).then((response) => {

            if (response.status === 201) {
                props.reload.setLoad(!props.reload.reload)
            } else if (response.status === 200) {
                alert(response.data.message)
            }
        })
            .catch((error) => {
                console.log(error)
            })
    }
    // 
    const navgatehandle = (e) => {
        e.stopPropagation()
        const data = JSON.stringify(props)
        /*ulr/  */
        navigate(`/postcontent/${props.id}`, { state: data });
    };
    // url
    const sharehandler = (e) => {
        e.stopPropagation()
        const pathname = `http://localhost:3000/postcontent/${props.id}`
        navigator.clipboard.writeText(pathname).then(() => {
            console.log("");
        }, (error) => {
            console.log("", error);
        });
    }
    const deletePostApi = () => {
        if (localStorageData.userid === props.postUserId) {
            deletePost(props.id).then(() => {
                props.reload.setLoad(!props.reload.reload)
                props.setreloadUserArticle(!props.reloadUserArticle)
            }).catch((error) => {
                console.log(error)
            })
        } else {
            alert('error')
        }
    }
    const targetIDHandler = (id) => {
        if (targetID) {
            setTargetID('')
        } else {
            setTargetID(id)
        }
    }
    class PostIcon {
        constructor(path) {
            {/* global process*/ }
            this.path = process.env.PUBLIC_URL + path
        }
    }
    const postIcon1 = new PostIcon(likeIcon)
    const postIcon2 = new PostIcon(commentIcon)
    const postIcon3 = new PostIcon(startIcon)
    const postIcon4 = new PostIcon(shareIcon)
    const postIcon11 = new PostIcon(likeIPichcon)
    const postIcon22 = new PostIcon(commentPichIcon)
    const postIcon33 = new PostIcon(startPichIcon)
    const postIcon44 = new PostIcon(sharePichIcon)

    const reportApi=()=>{
        alert('')
    }

    return (
        <div className='flex flex-col text-[--fontColor] justify-center mb-6 border-[--boxColor] hover:border-[--boxHoverColor] border-solid border-2  rounded-my-rounded-10px  p-2' onClick={navgatehandle}>
            <div className='flex flex-col space-y-2 '>
                <div className='flex flex-row justify-between items-center' onClick={e => e.stopPropagation()} style={{ justifyContent: 'space-between', fontWeight: 'bold' }}>
                    <div className='flex flex-row space-x-2 items-center'>
                        <img src={props.headimg} className='w-10 h-10 rounded-full'></img>
                        <Link className='font-blod '>{props.name}</Link>
                    </div>
                    <div onClick={() => targetIDHandler(props.id)}  className='relative'>...
                        {targetID === props.id ?
                            <DeleteBox postUserId={props.postUserId} deleteHandler={deletePostApi} DeleteBox={postDeleteBox} reportHandler={reportApi}></DeleteBox>
                            : null}
                    </div>
                </div>
                <div className='felx flex-col space-y-2'>
                    {props.content}
                    {(props.postImages.length === 0 || props.postImages === '') ? null : (<img src={props.postImages} className='w-full h-full'></img>)}
                </div>
                <div className='flex flex-row justify-around h-5 text-[--fontColor]' >
                    <div className='flex flex-row items-center hover:text-[host]' onClick={likehandle}
                        onMouseEnter={() => setMouseOver({ ...mouseOver, like: true })}
                        onMouseLeave={() => setMouseOver({ ...mouseOver, like: false })}
                    >
                        {mouseOver.like ?
                            <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon11.path} alt=''></img>
                            :
                            <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon1.path} alt=''></img>}
                        {props.likes}
                    </div>
                    <div className='flex flex-row items-center'
                        onMouseEnter={() => setMouseOver({ ...mouseOver, comments: true })}
                        onMouseLeave={() => setMouseOver({ ...mouseOver, comments: false })}>
                        {mouseOver.comments ?
                            <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon22.path} alt=''></img>
                            :
                            <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon2.path} alt=''></img>}
                        {props.comments}
                    </div>
                    <div className='flex flex-row items-center'
                        onClick={favoritehandler}
                        onMouseEnter={() => setMouseOver({ ...mouseOver, star: true })}
                        onMouseLeave={() => setMouseOver({ ...mouseOver, star: false })}>
                        {
                            mouseOver.star ?
                                <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon33.path} alt=''></img>
                                :
                                <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon3.path} alt=''></img>

                        }
                        {props.favorites}
                    </div>
                    <div className='flex flex-row items-center'
                        onClick={sharehandler}
                        onMouseEnter={() => setMouseOver({ ...mouseOver, share: true })}
                        onMouseLeave={() => setMouseOver({ ...mouseOver, share: false })}>
                        {mouseOver.share ?
                            <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon44.path} alt=''></img>
                            :
                            <img style={{ width: '100%', height: '100%', verticalAlign: 'middle', textAlign: 'center' }} src={postIcon4.path} alt=''></img>
                        }
                    </div>
                </div>
            </div>
        </div >
    )
}
Post.propTypes = {
    id: PropTypes.string.isRequired,
    headimg: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    content: PropTypes.string.isRequired,
    postImages: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.arrayOf(PropTypes.string),
      ]),
    likes: PropTypes.number.isRequired,
    comments: PropTypes.number.isRequired,
    favorites: PropTypes.number,
    postUserId: PropTypes.string.isRequired,
    reload: PropTypes.shape({
        reload: PropTypes.bool.isRequired,
        setLoad: PropTypes.func.isRequired,
    }),
    reloadUserArticle: PropTypes.bool,
    setreloadUserArticle: PropTypes.func,
};