import React, { useState, useEffect } from 'react'
import style from './UserHomePage.module.css'
import UserArticle from './UserArticle'
import UserComment from './UserComment'
import UserReply from './UserReply'
import { useNavigate } from 'react-router-dom'
import { getSocket } from '../../../../socket/socket'
import useLocalStorage from '../../../../hooks/useLocaStorage'
export default function UserHomePage() {
  /*   */
  //  
  const [seletcpage, setseletcpage] = useState('')
  const Navigate = useNavigate()
  // const [localStorageData, setLocalStorage] = useState({})
  // useEffect(() => {
  //   if (localStorage.getItem('loginData')) {
  //     setLocalStorage(JSON.parse(localStorage.getItem('loginData')))
  //   }
  // }, [])
 const[localStorageData]=useLocalStorage()
  //  switch
  const rendercompents = () => {
    switch (seletcpage) {
      case "UserArticle": return <UserArticle localStorageData={localStorageData}></UserArticle>
      case "UserComment": return <UserComment localStorageData={localStorageData}></UserComment>
      case "UserReply": return <UserReply localStorageData={localStorageData}></UserReply>
      default: return <UserArticle localStorageData={localStorageData}></UserArticle>
    }
  }
  // 
  const closeAnAccountHanlder = () => {
    localStorage.removeItem("loginData")
    Navigate("/", { replace: true })
    window.location.reload();
    const socket = getSocket()
    socket.disconnect()
  }
  return (
    <div className='w-full p-2'>
      <div className='flex items-center justify-between w-full'>
        <div className='flex items-center  '>
          <div className='w-10  h-10 rounded-full'>
            <img src={localStorageData.headimg} className='w-10  h-10 rounded-full' alt=""></img>
          </div>
          <p className='text-[--fontColor] font-bold '> {localStorageData.username}</p>
        </div>
        <div className='flex space-x-2'>
          <div className='text-[--assistantColor]' onClick={() => Navigate("/setting")}></div> 
          <div className='text-[--fontColor]' onClick={closeAnAccountHanlder}></div>
        </div>
      </div>
      <div className='text-[fontColor] flex justify-between mt-2 mb-2'>
        <div className='hover:text-[--assistantColor]' onClick={() => { setseletcpage('UserArticle') }}></div>
        <div className='hover:text-[--assistantColor]' onClick={() => { setseletcpage('UserComment') }}></div>
        <div className='hover:text-[--assistantColor]' onClick={() => { setseletcpage('UserReply') }}></div>
      </div >
      {rendercompents()}
    </div>
  )
}
